.vote-container {
  max-width: 800px;
  padding: 1rem;
  background-color: #463b3b;
}

.round-info {
  height: 15vh;
  background-color: #f5f5f5;
  border-radius: 4px;
  h2 {
    text-align: center;
    font-weight: 800;
    font-size: 20pt;
    padding: 1rem 0;
  }
  h3 {
    margin: 1rem;
  }
}

.vote-buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: .7rem;
  height: 80vh;
}

.vote-btn {
  padding: 2rem;
  font-size: 18pt;
  border: none;
  border-radius: 8px;
  background-color: #2f2f2f;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.vote-btn:hover:not(.disabled) {
  background-color: #1976D2;
}

.vote-btn.disabled {
  background-color: #ccc;
  cursor: not-allowed;
}